* {
    font-family: "Agency FB", serif;
    font-weight: bold;
}

.courseNode {
    width: 180px;
    height: 60px;
    background-color: white;
    box-shadow: -3px 3px 5px rgba(0, 0, 0, 0.5), inset 3px -3px 5px rgba(0, 0, 0, 0.5), inset -3px 3px 5px rgba(255, 255, 255, 0.5);
    border-style: solid;
    border-width: 2px;
    border-color: black;
    border-radius: 20px;
}

.courseId {
    height: 50%;
    width: 90%;
    font-size: 20px;
    margin-top: 4px;
}

.name {
    height: 50%;
    width: 75%;
    font-size: 12px;
    margin-top: 25px;
    overflow: auto;
}

.center {
    position: absolute;
    top: 50%;
    left: 50%;
    -ms-transform: translateX(-50%) translateY(-50%);
    transform: translateX(-50%) translateY(-50%);

    text-align: center;
}

.top-center {
    position: absolute;
    left: 50%;
    -ms-transform: translateX(-50%);
    transform: translateX(-50%);
    text-align: center;
}

.middle-left {
    position: absolute;
    top: 50%;
    -ms-transform: translateY(-50%);
    transform: translateY(-50%);
    text-align: center;
}

.credit {
    width: 8px;
    height: 8px;
    margin-top: 2.5px;
    margin-bottom: 2.5px;
    border-style: solid;
    border-radius: 50%;
    border-width: 1px;
    border-color: black;
    top: 100px;
    background-image: radial-gradient(farthest-corner at 65% 35%, white 0, rgb(200, 200, 200) 50%, rgb(128, 128, 128) 100%)
}

.creditGroup {
    margin-left: 5px;
    width: fit-content;
    height: fit-content;
}

.bg-math {
    background-image: linear-gradient(to right, rgb(0, 200, 0), rgb(0, 255, 0));
}

.bg-comp {
    background-image: linear-gradient(to right, rgb(0, 200, 200), rgb(0, 255, 255));
}